// MiddleBlock/index.tsx
import React, { useState, useEffect, useRef } from 'react';
import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { StyledButton } from "../../common/Button/styles";
import { StyledSubtitle } from './StyledMiddleBlock';
import {
  MiddleBlockSection,
  Content,
  ContentWrapper,
  Title,
  Subtitle,
  ImageWrapper,
  GreyArea,
} from "./styles";
import { splitTitleIntoWords, wrapParagraphs } from "../../common/utils/textAnimations";

interface MiddleBlockProps {
  title: string;
  subtitle: string;
  content: string;
  button: string;
  id?: string;
  t: any;
}

const MiddleBlock = ({ title, subtitle, content, button, id, t }: MiddleBlockProps) => {
  const [isVisible, setIsVisible] = useState(false);
  const [animationKey, setAnimationKey] = useState(0); // State to trigger re-animation
  const ref = useRef(null);

  useEffect(() => {
    const thresholdArray = window.innerWidth <= 768 ? [0.3] : [0.1];

    const observer = new IntersectionObserver(
      ([entry]) => {
        setIsVisible(entry.isIntersecting);
        if (entry.isIntersecting) {
          setAnimationKey(prevKey => prevKey + 1); // Increment key to re-trigger animations
        }
      },
      { threshold: thresholdArray }
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, []);

  const scrollToContactForm = () => {
    document.getElementById('contact')?.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <MiddleBlockSection ref={ref} id={id} isVisible={isVisible}>
      <Row justify="start" align="middle">
        <Title key={`title-${animationKey}`}>{splitTitleIntoWords(t(title))}</Title>
        <ContentWrapper>
          <Col lg={12} md={24} sm={24} xs={24}>
            <GreyArea isVisible={isVisible} key={`grey-area-${animationKey}`}>
              <StyledSubtitle key={`subtitle-${animationKey}`}>{t(subtitle)}</StyledSubtitle> {/* Subtitle inside GreyArea */}
              <Content key={`content-${animationKey}`}>
                {wrapParagraphs(t(content))}
              </Content>
              <StyledButton onClick={scrollToContactForm} glowing={true} fixedWidth={true}>
                {t(button)}
              </StyledButton>
            </GreyArea>
          </Col>
          <Col lg={12} md={24} sm={24} xs={24}>
            <ImageWrapper isVisible={isVisible} key={`image-wrapper-${animationKey}`}>
              <video autoPlay loop muted playsInline className="intro-video">
                <source src="/img/video/phone-video.webm" type="video/webm" />
                Your browser does not support the video tag.
              </video>
              </ImageWrapper>
          </Col>
        </ContentWrapper>
      </Row>
    </MiddleBlockSection>
  );
};
export default withTranslation()(MiddleBlock);
