import React, { ReactElement } from 'react';

// my-app/src/common/utils/textAnimations.tsx

export const splitTitleIntoWords = (title: string) => {
  return title.split(' ').map((word, index) => (
    <React.Fragment key={index}>
      <span className={`word word-${index}`}>{word}</span>{' '}
    </React.Fragment>
  ));
};

export const wrapParagraphs = (text: string): ReactElement[] => {
  let wordIndex = 0;
  let elements: ReactElement[] = [];
  const words = text.split(' ');

  words.forEach((word, wordIdx) => {
    const parts = word.split('\n');
    parts.forEach((part, partIdx) => {
      if (partIdx > 0) {
        elements.push(<br key={`br-${wordIndex}`} />);
        wordIndex++;
      }
      if (part) {
        // Check if the part ends with a colon and merge it with the next word
        let modifiedPart = part;
        let nextPart = wordIdx < words.length - 1 ? words[wordIdx + 1] : "";
        if (part.endsWith(':') && nextPart) {
          modifiedPart += nextPart;
          words[wordIdx + 1] = ""; // Empty the next word as it has been merged
        }
        
        const className = ['Film Industry Expertise', 'Musical Legacy', 'Physical Health & Science Expertise', 'Our impact'].includes(modifiedPart) ? 'underline' : '';
        elements.push(
          <span key={`part-${wordIndex}`} className={`letter letter-${wordIndex} ${className}`}>
            {modifiedPart}
          </span>
        );
        wordIndex++;
      }
    });
    if (wordIdx < words.length - 1) {
      elements.push(
        <span key={`space-${wordIndex}`} className="letter">
          &nbsp;
        </span>
      );
      wordIndex++;
    }
  });

  return elements;
};