// StyledMiddleBlock.tsx
import styled, { keyframes } from 'styled-components';

// Define the keyframes for the fadeIn animation
const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`;

export const StyledSubtitle = styled.h2`
  font-size: 2em; // Default font size for larger screens
  font-weight: bold;
  text-align: center;
  color: #fff;
  margin: 20px 0;
  animation: ${fadeIn} 1s ease forwards;

  // Media query for mobile devices
  @media only screen and (max-width: 768px) {
    font-size: 1.3em; // Smaller font size for mobile screens
    width: 100%;
    // You can adjust the margin, padding, etc., for mobile here if needed
  }

  // You can add more media queries for different breakpoints if needed
`;

// Styled component for the MiddleBlock
const StyledMiddleBlock = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
  background-color: #1c1c1c; // Dark background for contrast
  animation: ${fadeIn} 2s ease forwards;
  animation-delay: 3s; // Delay the appearance
  border: 2px solid #fff; // Basic border, replace with your color preference

  // Glowing border effect with neon reds and pinks
  box-shadow: 
    0 0 5px #ff2d2d, // Innermost shadow for tight glow
    0 0 15px #ff2d2d, // Medium glow
    0 0 30px #fff, // Larger glow with a pink hue

  // Ensuring the glow effect is smooth and doesn't cause layout shift
  margin: 0 auto;
  overflow: hidden;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: -2px; // Align with the border size
    right: -2px; // Align with the border size
    bottom: -2px; // Align with the border size
    left: -2px; // Align with the border size
    z-index: -1;
    background: inherit;
    border-radius: inherit; // Adjust this if your block has rounded corners
  }
`;

export default StyledMiddleBlock;
